import { useContext, useEffect, useState } from "react";

import { BigNumber, ethers } from "ethers";
import sushiData from "@sushiswap/sushi-data";
import useAsyncEffect from "use-async-effect";
import Fraction from "../constants/Fraction";
import { EthersContext } from "../context/EthersContext";
import LPTokenWithValue from "../types/LPTokenWithValue";
import { isWETH } from "../utils";
import { getCirculatingBTCSTSupply,getBridgeTokenPair, bridgeActions,totalSupplyOfSToken,getTotalRemainingSupplyLocked} from "../utils/api-utils";
import { getContract, parseBalance } from "../utils";
import { BTCST,BTCSTFarm,BBTC,BRIDGE } from "../constants/contracts";
import MiningUserInfo from "../types/MiningUserInfo";
import MiningStakeRecord from "../types/MiningStakeRecord";
import {isProviderOnTron,getAsyncContract} from "../utils/getContract"
import {TronWebProvider} from "../connectors/tron/tronWebProvider"
import {TRON_ADDRESS_MAPPING} from "../constants/contracts"


export enum STATUS{INIT,ARRIVED,ROLLEDBACK,COMMITED,WAIT_SELF_CLAIM};
export const STATUS_MAP = {
    0:"initial",
    1:"arrived",
    2:"rolledback",
    3:"commited",
    4:"claimable"
}
export interface BridgeRecord{
    cts:BigNumber;
    uts:BigNumber;
    otherSideAddress:string;
    status:BigNumber;
    amount:BigNumber;
}
export interface RecordState {
    totalLockedBTCST:ethers.BigNumber;
    loadingTotalLocked:boolean;
    bridgedToTron:ethers.BigNumber;
    loadingbridgedToTron:boolean;
    records?:BridgeRecord[];
    loadingRecords:boolean;
}

// tslint:disable-next-line:max-func-body-length
const useRecordState = () => {
    const { provider, signer, address } = useContext(EthersContext);
    const {getUserRecordLen,getUserRecord} = bridgeActions();
    const [totalLockedBTCST,settotalLockedBTCST] = useState<ethers.BigNumber>();
    const [loadingTotalLocked,setloadingTotalLocked] = useState(true);
    
    
    const [bridgedToTron,setbridgedToTron] = useState<ethers.BigNumber>();
    const [loadingbridgedToTron,setloadingbridgedToTron] = useState(true);

    const [records,setRecords] = useState<BridgeRecord[]>();
    const [loadingRecords,setLoadingRecords] = useState(true);

    useEffect(() => {
        settotalLockedBTCST(undefined);
        setloadingTotalLocked(true);
        setbridgedToTron(undefined);
        setloadingbridgedToTron(true);
        setRecords(undefined);
        setLoadingRecords(true);
    }, [address]);

    //load total mined BTC in all
    useAsyncEffect(async()=>{
        if (provider && signer) {
            const contract = getContract("ERC20", BTCST, signer);
            try{
                // const pair = await getBridgeTokenPair(provider);
                // console.log("pair",pair);
                
                // settotalLockedBTCST(await contract.balanceOf(BRIDGE));
            }finally{
                
            }
        }
    },[provider,signer]);
    //load current total staked btcst
    useAsyncEffect(async()=>{
        if (provider && signer ){
            setloadingbridgedToTron(true);
            setloadingTotalLocked(true);
            try{
                let total = await getCirculatingBTCSTSupply(provider);
                if ( !isProviderOnTron(provider) && !isProviderOnTron(signer)){
                    const locked = await getTotalRemainingSupplyLocked(provider);
                    total = total.sub(locked);
                }
                const pair = await getBridgeTokenPair(provider);
                const left = pair["leftAmount"];
                // settotalLockedBTCST(total.sub(left));
                if (isProviderOnTron(provider)){
                    setbridgedToTron(total);
                    settotalLockedBTCST(undefined);
                }else{
                    settotalLockedBTCST(total);
                    setbridgedToTron(left); 
                }
                
            }finally{
                setloadingbridgedToTron(false);
                setloadingTotalLocked(false);
            }
        }
    },[provider,signer]);
    useAsyncEffect(async()=>{
        if (provider && signer ){
            setLoadingRecords(true);
            try{
                let account;
                if ( !isProviderOnTron(provider) && !isProviderOnTron(signer)){
                    account = await signer.getAddress();
                }else{
                    const ppp = signer.provider.provider as TronWebProvider;
                    account = ppp.getAccount().hex;
                }
                const len = await getUserRecordLen(account,provider);
                const MAX_LOAD = 10;
                let load = len;
                if (len>MAX_LOAD){
                    load = MAX_LOAD;
                }
                if (len>0){
                    let result:BridgeRecord[] = [];
                    for (let ii=len-1;ii>=0;ii--){
                        let record = await getUserRecord(BigNumber.from(ii),account,provider);
                        result.push({
                            cts:record['cts'],
                            uts:record['uts'],
                            otherSideAddress:record['otherSideInfo']['otherSideAddress'],
                            status:record['status'],
                            amount:record['amount']
                        } as BridgeRecord);
                    }
                    setRecords(result);
                }

            }finally{
                setLoadingRecords(false);
            }
        }
    },[provider,signer]);
    
    return {
        totalLockedBTCST,
        loadingTotalLocked,
        bridgedToTron,
        loadingbridgedToTron,
        records,
        loadingRecords
    };
};

export default useRecordState;
