import React, { FC, useCallback, useContext, useMemo } from "react";
import { FlatList, Platform, TouchableHighlight, View } from "react-native";
import { Icon } from "react-native-elements";

import { BigNumber, ethers } from "ethers";
import BackgroundImage from "../components/BackgroundImage";
import Border from "../components/Border";
import Container from "../components/Container";
import Content from "../components/Content";
import FlexView from "../components/FlexView";
import Heading from "../components/Heading";
import Loading from "../components/Loading";
import Text from "../components/Text";
import Title from "../components/Title";

import WebFooter from "../components/web/WebFooter";
import { IS_DESKTOP, Spacing } from "../constants/dimension";
import { EthersContext } from "../context/EthersContext";
import useColors from "../hooks/useColors";

import useRecordState, { RecordState,BridgeRecord,STATUS,STATUS_MAP } from "../hooks/useBridgeRecordState";
import useLinker from "../hooks/useLinker";
import useTranslation from "../hooks/useTranslation";
import LPTokenWithValue from "../types/LPTokenWithValue";
import TokenWithValue from "../types/TokenWithValue";
import { formatUSD,formatBalance, formatTimeKey,formatTronAddress } from "../utils";
import Screen from "./Screen";
import MiningStakeRecord from "../types/MiningStakeRecord";
import { BridgeSubMenu } from "../components/web/WebSubMenu";

interface BridgeRecordItemProps {
    record: BridgeRecord;
    disabled?: boolean;
}
const SHTMiningScreen = () => {
    const t = useTranslation();
    const state = useRecordState();
    const { } = useContext(EthersContext);
    const loading = state.loadingTotalLocked;
    const totalValue = state.totalLockedBTCST;
    
    return (
        <Screen>
            <Container>
                <BackgroundImage />
                <Content style={{ paddingBottom: Spacing.huge }}>
                    <Title text={t("ourBridge.stay-on-BSC")} style={{ flex: 1,fontSize:28 }} />
                    <Title
                        text={loading ? t("fetching") : totalValue==undefined? "N/A":formatBalance(totalValue,18,8)}
                        fontWeight={"light"}
                        disabled={loading}
                        style={{ fontSize: IS_DESKTOP ? 32 : 24 }}
                    />
                    <Mining state={state} />
                </Content>
                {Platform.OS === "web" && <WebFooter />}
            </Container>
            <BridgeSubMenu />
        </Screen>
    );
};

const Mining = ({ state }: { state: RecordState }) => {
    return (
        <View style={{ marginTop: IS_DESKTOP ? Spacing.large : Spacing.normal }}>
            <BtcStillInPool state={state} />
            <Border/>
            <View style={{ height: Spacing.tiny }} />
            <YourStakingRecord state={state} />
        </View>
    );
};

const BtcStillInPool = ({ state }: { state: RecordState }) => {
    const t = useTranslation();
    const {} = useContext(EthersContext);
    const amount = state.bridgedToTron;
    const loading = state.loadingbridgedToTron;
    return (
        <View>
            <Heading text={t("ourBridge.already-bridged-to-tron")} 
                style={{ flex: 1,fontSize:28 }}/>
            <Title
                        text={loading || amount==undefined ? t("fetching") : formatBalance(amount,18,8)}
                        fontWeight={"light"}
                        disabled={loading}
                        style={{ fontSize: IS_DESKTOP ? 32 : 24 }}
                    />
        </View>
    );
};
const YourStakingRecord = ({ state }: { state: RecordState }) => {
    const t = useTranslation();
    
    return (
        <View>
            <Heading text={t("ourBridge.your-bridge-records")} />
            {/* @ts-ignore */}
            <RecordList loading={state.loadingRecords} records={state.records} recordItem={RecordItem}/>
        </View>
    );
};



const LoadingNumber = (props:{
    loading:boolean;
    number:ethers.BigNumber;
    suffix:string;
})=>{
    // const text = formatBalance(props.number)+""+props.suffix;
    const suffix = props.suffix?props.suffix:"";
    return (props.loading || props.number==undefined) ?(
        <Loading />
    ):(
        <Title text={formatBalance(props.number)+""+suffix} fontWeight={"light"} disabled={props.loading}
            style={{ fontSize: IS_DESKTOP ? 32 : 24 }}
        />
    );
}
const RecordList = (props: {
    loading: boolean;
    records?: BridgeRecord[];
    recordItem: FC<BridgeRecordItemProps>;
}) => {
    const t = useTranslation();
    const renderItem = useCallback(({ item }) => {
        return <props.recordItem key={item.cts} record={item} />;
    }, []);
    const data = useMemo(
        () =>(props.records || [])
                // @ts-ignore
                .sort((t1, t2) => (t2.cts.toNumber() || 0) - (t1.cts.toNumber() || 0)),
        [props.records]
    );
    return props.loading ? (
        <Loading />
    ) : data.length === 0 ? (
        <EmptyList />
    ) : (
        <View>
            <FlexView style={{ alignItems: "center", paddingHorizontal: Spacing.tiny, paddingVertical: 4 }}>
                <View style={{flex:1,alignItems:"flex-start"}}>
                    <Text caption={true} numberOfLines={1} fontWeight={"light"}>
                        {t("date")}
                    </Text>
                </View>
                <View>
                    <Text caption={true} numberOfLines={1}  style={{ marginLeft: Spacing.small}}>
                        {t("ourBridge.to")}
                    </Text>
                </View>
                <View>
                    <Text caption={true} numberOfLines={1}  style={{ marginLeft: Spacing.small}}>
                        {t("amount")}
                    </Text>
                </View>
                <View style={{ flex: 1, alignItems: "flex-end" }}>
                    <Text caption={true} fontWeight={"light"} >
                        {t("status")}
                    </Text>
                </View>
            </FlexView>
            <FlatList
                keyExtractor={item => item.cts.toString()}
                data={data}
                renderItem={renderItem}
                ItemSeparatorComponent={() => <Border small={true} />}
            />
        </View>
    );
};
const EmptyList = () => {
    const t = useTranslation();
    return (
        <View style={{ margin: Spacing.normal }}>
            <Text disabled={true} style={{ textAlign: "center", width: "100%" }}>
                {t("ourBridge.empty-bride-records")}
            </Text>
        </View>
    );
};
const RecordItem = (props: BridgeRecordItemProps) => {
    const t = useTranslation();
    const record = props.record;
    return (
        <FlexView style={{ alignItems: "center", paddingHorizontal: Spacing.tiny, paddingVertical: 4 }}>
            <View style={{flex:1,alignItems:"flex-start"}}>
                <Text caption={true} numberOfLines={1} fontWeight={"light"}
                    disabled={props.disabled}>
                    {formatTimeKey(record.cts)}
                </Text>
            </View>
            <View>
                <Text caption={true} numberOfLines={1}  
                    style={{ marginLeft: Spacing.small}}
                    disabled={props.disabled}>
                    {formatTronAddress(record.otherSideAddress)}
                </Text>
            </View>
            <View>
                <Text caption={true} numberOfLines={1}  
                    style={{ marginLeft: Spacing.small}}
                    disabled={props.disabled}>
                    {formatBalance(record.amount)}
                </Text>
            </View>
            <View style={{ flex: 1, alignItems: "flex-end" }}>
                <Text caption={true} fontWeight={"light"} disabled={props.disabled}>
                    {t("ourBridge."+STATUS_MAP[record.status.toNumber()])}
                </Text>
            </View>
            {/* <ExternalIcon path={"/slot/" + record.cts} /> */}
        </FlexView>
    );
};
const ExternalIcon = ({ path }) => {
    const { textDark, disabled } = useColors();
    const onPress = () => window.open("https://app.btcst.finance/#/history" + path.toLowerCase());
    const isETH = path.endsWith(ethers.constants.AddressZero);
    return (
        <TouchableHighlight onPress={onPress} disabled={isETH}>
            <Icon
                type={"evilicon"}
                name={"external-link"}
                color={isETH ? disabled : textDark}
                style={{ marginLeft: Spacing.tiny }}
            />
        </TouchableHighlight>
    );
};
export default SHTMiningScreen;
